@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
/* Google fonts */

/* normalize values  */
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* main variables  */

:root {
  /* Dark Theme (Default) */
  --color-bg: #0a1128;
  --color-bg-variant: rgba(255, 255, 255, 0.05);
  --color-primary: #64ffda;
  --color-primary-variant: rgba(100, 255, 218, 0.4);
  --color-secondary: #ccd6f6;
  --color-white: #e6f1ff;
  --color-light: rgba(230, 241, 255, 0.6);
  
  /* Glass Effect */
  --glass-bg: rgba(255, 255, 255, 0.05);
  --glass-border: rgba(255, 255, 255, 0.1);
  --glass-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.3);
  
  /* Container Sizes */
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  
  /* Other Variables */
  --transition: all 0.3s ease;
  --border-radius: 0.5rem;
}

/* Light theme */
body.light-theme {
  --color-bg: #fafafa;
  --color-bg-variant: rgba(0, 0, 0, 0.05);
  --color-primary: #00b7c7;
  --color-primary-variant: rgba(0, 183, 199, 0.4);
  --color-secondary: #2d3748;
  --color-white: #1a202c;
  --color-light: rgba(26, 32, 44, 0.6);
  
  /* Glass Effect Light Theme */
  --glass-bg: rgba(255, 255, 255, 0.7);
  --glass-border: rgba(255, 255, 255, 0.2);
  --glass-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.1);
}

/* Background Effect */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 0% 0%, rgba(100, 255, 218, 0.4) 0%, transparent 20%),
    radial-gradient(circle at 100% 0%, rgba(100, 255, 218, 0.4) 0%, transparent 20%),
    radial-gradient(circle at 100% 100%, rgba(100, 255, 218, 0.4) 0%, transparent 20%),
    radial-gradient(circle at 0% 100%, rgba(100, 255, 218, 0.4) 0%, transparent 20%);
  background: 
    radial-gradient(circle at 0% 0%, var(--color-primary-variant) 0%, transparent 20%),
    radial-gradient(circle at 100% 0%, var(--color-primary-variant) 0%, transparent 20%),
    radial-gradient(circle at 100% 100%, var(--color-primary-variant) 0%, transparent 20%),
    radial-gradient(circle at 0% 100%, var(--color-primary-variant) 0%, transparent 20%);
  opacity: 0.4;
  z-index: -1;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #0a1128;
  background: var(--color-bg);
  color: #e6f1ff;
  color: var(--color-white);
  line-height: 1.7;
  transition: all 0.3s ease;
  transition: var(--transition);
}

/* Glass Container Style */
.glass-container {
  background: rgba(255, 255, 255, 0.05);
  background: var(--glass-bg);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border: 1px solid var(--glass-border);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.3);
  box-shadow: var(--glass-shadow);
  border-radius: 0.5rem;
  border-radius: var(--border-radius);
}

/* General Styles */
.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
  line-height: 1.2;
}

h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  margin-bottom: 1rem;
}

section {
  margin-top: 8rem;
  padding: 2rem;
  border-radius: 0.5rem;
  border-radius: var(--border-radius);
}

section > h2,
section > h5 {
  text-align: center;
  color: rgba(230, 241, 255, 0.6);
  color: var(--color-light);
}

section > h2 {
  color: #64ffda;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(230, 241, 255, 0.6);
  color: var(--color-light);
}

a {
  color: #64ffda;
  color: var(--color-primary);
  transition: all 0.3s ease;
  transition: var(--transition);
  position: relative;
}

a:hover {
  color: #e6f1ff;
  color: var(--color-white);
}

/* Underline animation for links */
a:not(.btn):after {
  content: '';
  position: absolute;
  width: 0;
  height: 1px;
  bottom: -2px;
  left: 0;
  background-color: #64ffda;
  background-color: var(--color-primary);
  transition: all 0.3s ease;
  transition: var(--transition);
}

a:not(.btn):hover:after {
  width: 100%;
}

.btn {
  display: inline-flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  border-radius: var(--border-radius);
  cursor: pointer;
  border: 1px solid #64ffda;
  border: 1px solid var(--color-primary);
  color: #64ffda;
  color: var(--color-primary);
  background: rgba(255, 255, 255, 0.05);
  background: var(--glass-bg);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  transition: all 0.3s ease;
  transition: var(--transition);
}

.btn:hover {
  background: #64ffda;
  background: var(--color-primary);
  color: #0a1128;
  color: var(--color-bg);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.btn-primary {
  background: #64ffda;
  background: var(--color-primary);
  color: #0a1128;
  color: var(--color-bg);
}

.btn-primary:hover {
  background: transparent;
  color: #64ffda;
  color: var(--color-primary);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
  border-radius: 0.5rem;
  border-radius: var(--border-radius);
  transition: all 0.3s ease;
  transition: var(--transition);
}

img:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  background: var(--color-bg-variant);
}

::-webkit-scrollbar-thumb {
  background: rgba(100, 255, 218, 0.4);
  background: var(--color-primary-variant);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #64ffda;
  background: var(--color-primary);
}

/* Media Queries */
@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }

  section {
    margin-top: 2rem;
    padding: 1rem;
  }
}
footer {
    background: var(--color-bg-variant);
    padding: 4rem 0 2rem;
    margin-top: 7rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer__container {
    width: var(--container-width-lg);
    margin: 0 auto;
    padding: 0 1rem;
}

.footer__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4rem;
}

.footer__logo {
    text-align: center;
}

.footer__logo h3 {
    color: var(--color-white);
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.footer__logo small {
    color: var(--color-light);
    font-size: 0.9rem;
}

.footer__nav ul {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}

.footer__nav a {
    color: var(--color-light);
    font-size: 0.9rem;
    transition: all 0.3s ease;
}

.footer__nav a:hover {
    color: var(--color-primary);
}

.footer__social {
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.footer__social a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height: 35px;
    background: var(--color-bg);
    color: var(--color-white);
    border-radius: 50%;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.footer__social a:hover {
    background: var(--color-primary);
    color: var(--color-bg);
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
}

.footer__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 2rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer__copyright p,
.footer__location p {
    color: var(--color-light);
    font-size: 0.85rem;
}

/* Media Queries (Medium Devices) */
@media screen and (max-width: 1024px) {
    .footer__container {
        width: var(--container-width-md);
    }

    .footer__content {
        flex-direction: column;
        grid-gap: 2rem;
        gap: 2rem;
        text-align: center;
    }

    .footer__nav ul {
        justify-content: center;
    }
}

/* Media Queries (Small Devices) */
@media screen and (max-width: 600px) {
    footer {
        padding: 3rem 0 2rem;
        margin-top: 5rem;
    }

    .footer__container {
        width: var(--container-width-sm);
    }

    .footer__nav ul {
        flex-direction: column;
        align-items: center;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .footer__bottom {
        flex-direction: column;
        grid-gap: 1rem;
        gap: 1rem;
        text-align: center;
    }
}

/* Hover Animation */
@-webkit-keyframes socialBounce {
    0%, 100% { -webkit-transform: translateY(0); transform: translateY(0); }
    50% { -webkit-transform: translateY(-5px); transform: translateY(-5px); }
}
@keyframes socialBounce {
    0%, 100% { -webkit-transform: translateY(0); transform: translateY(0); }
    50% { -webkit-transform: translateY(-5px); transform: translateY(-5px); }
}

.footer__social a:hover {
    -webkit-animation: socialBounce 0.5s ease;
            animation: socialBounce 0.5s ease;
}
header {
  min-height: 100vh;
  padding: 7rem 0 2rem;
  overflow: hidden;
  background: linear-gradient(
    180deg,
    rgba(var(--color-bg-rgb), 0.8) 0%,
    rgba(var(--color-bg-rgb), 1) 100%
  );
}

.container.header__container {
  position: relative;
  height: 100%;
}

.header__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 4rem;
  gap: 4rem;
  align-items: center;
  height: calc(100vh - 9rem);
}

.header__text {
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.text-content {
  -webkit-animation: fadeInUp 1s ease;
          animation: fadeInUp 1s ease;
}

.greeting {
  font-size: 1.2rem;
  color: var(--color-light);
  position: relative;
  display: inline-block;
  padding-left: 1.5rem;
}

.greeting::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 1rem;
  height: 2px;
  background: var(--color-primary);
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.name {
  font-size: 3.5rem;
  line-height: 1.2;
  background: linear-gradient(
    90deg,
    var(--color-primary) 0%,
    var(--color-primary-variant) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  margin: 0.5rem 0;
  -webkit-animation: fadeInUp 1s ease 0.2s backwards;
          animation: fadeInUp 1s ease 0.2s backwards;
}

.title {
  font-size: 1.5rem;
  color: var(--color-white);
  margin-bottom: 1rem;
  -webkit-animation: fadeInUp 1s ease 0.4s backwards;
          animation: fadeInUp 1s ease 0.4s backwards;
}

.description {
  font-size: 1.1rem;
  color: var(--color-light);
  max-width: 500px;
  line-height: 1.6;
  -webkit-animation: fadeInUp 1s ease 0.6s backwards;
          animation: fadeInUp 1s ease 0.6s backwards;
}

.header__image {
  position: relative;
  justify-self: center;
}

.image-wrapper {
  width: 400px;
  height: 400px;
  position: relative;
  border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  -webkit-animation: profile__animate 8s ease-in-out infinite;
          animation: profile__animate 8s ease-in-out infinite;
}

.image-wrapper img {
  position: absolute;
  inset: 0;
  border-radius: inherit;
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.image-wrapper:hover img {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

/* Floating Elements */
.floating-elements {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.floating-tag {
  position: absolute;
  padding: 0.5rem 1rem;
  background: rgba(var(--color-bg-variant-rgb), 0.9);
  border: 1px solid rgba(var(--color-primary-rgb), 0.2);
  border-radius: 1rem;
  font-size: 0.9rem;
  color: var(--color-primary);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.floating-tag.react {
  top: 10%;
  right: -20%;
  -webkit-animation: float 6s ease-in-out infinite;
          animation: float 6s ease-in-out infinite;
}

.floating-tag.js {
  bottom: 20%;
  left: -20%;
  -webkit-animation: float 6s ease-in-out infinite 2s;
          animation: float 6s ease-in-out infinite 2s;
}

.floating-tag.css {
  top: 50%;
  right: -15%;
  -webkit-animation: float 6s ease-in-out infinite 4s;
          animation: float 6s ease-in-out infinite 4s;
}

@-webkit-keyframes float {
  0%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@keyframes float {
  0%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes profile__animate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}

@keyframes profile__animate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}

/* CTA Styles */
.cta {
  margin-top: 2rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  -webkit-animation: fadeInUp 1s ease 0.8s backwards;
          animation: fadeInUp 1s ease 0.8s backwards;
}

.cta a {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.cta a:first-child {
  background: var(--color-primary);
  color: var(--color-bg);
  position: relative;
  overflow: hidden;
}

.cta a:first-child::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: width 0.5s ease, height 0.5s ease;
}

.cta a:first-child:hover::before {
  width: 200%;
  height: 200%;
}

.cta a:last-child {
  background: transparent;
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
}

.cta a:last-child:hover {
  background: var(--color-primary);
  color: var(--color-bg);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

/* Socials Styles */
.header__socials {
  position: fixed;
  left: 2rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  z-index: 2;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

.header__socials a {
  color: var(--color-primary);
  font-size: 1.2rem;
  transition: all 0.3s ease;
  background: rgba(var(--color-bg-rgb), 0.1);
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba(var(--color-primary-rgb), 0.2);
}

.header__socials a:hover {
  color: var(--color-white);
  background: var(--color-primary);
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}

/* Scroll Down */
.scroll__wrapper {
  position: absolute;
  right: -1rem;
  bottom: 8rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.scroll__down {
  display: flex;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  color: var(--color-primary);
  transition: var(--transition);
  font-size: 0.9rem;
}

.scroll__down:hover {
  color: var(--color-white);
  -webkit-transform: translateX(4px);
          transform: translateX(4px);
}

.scroll__icon {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  width: 23px;
  height: 32px;
  border: 2px solid var(--color-primary);
  border-radius: 14px;
  position: relative;
}

.scroll__icon::before {
  content: '';
  width: 4px;
  height: 6px;
  background: var(--color-primary);
  border-radius: 2px;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 6px;
  -webkit-animation: scroll 2s infinite;
          animation: scroll 2s infinite;
}

@-webkit-keyframes scroll {
  0% {
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(-50%, 10px);
            transform: translate(-50%, 10px);
    opacity: 0;
  }
}

@keyframes scroll {
  0% {
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(-50%, 10px);
            transform: translate(-50%, 10px);
    opacity: 0;
  }
}

/* Theme Toggle */
.theme-toggle {
  position: fixed;
  right: 4rem;
  top: 1rem;
  z-index: 1001;
  background: var(--color-primary);
  color: var(--color-bg);
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  outline: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.theme-toggle:hover {
  background: var(--color-white);
  color: var(--color-bg);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

/* Media Queries */
@media screen and (max-width: 1024px) {
  .header__content {
    grid-template-columns: 1fr;
    text-align: center;
    grid-gap: 2rem;
    gap: 2rem;
    padding-top: 4rem;
  }

  .header__text {
    align-items: center;
  }

  .description {
    margin: 0 auto;
  }

  .image-wrapper {
    width: 300px;
    height: 300px;
  }

  .floating-tag {
    display: none;
  }

  .header__socials {
    display: none;
  }

  .scroll__wrapper {
    right: -2rem;
    bottom: 2.5rem;
  }
  
  .theme-toggle {
    right: 2rem;
  }
}

@media screen and (max-width: 600px) {
  header {
    padding-top: 5rem;
  }

  .name {
    font-size: 2.5rem;
  }

  .title {
    font-size: 1.2rem;
  }

  .description {
    font-size: 1rem;
  }

  .image-wrapper {
    width: 250px;
    height: 250px;
  }

  .scroll__wrapper {
    display: none;
  }
}

#services {
  padding: 4rem 0;
  background: var(--color-bg);
}

.services__header {
  text-align: center;
  margin-bottom: 3rem;
}

.services__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding: 0 1.5rem;
}

.service {
  background: var(--color-bg-variant);
  border-radius: 1rem;
  border: 1px solid transparent;
  transition: all 0.3s ease;
}

.service:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.service__head {
  padding: 1.5rem;
